import React, {useState} from "react";
import columns from "./columns.js";
import TableRow from "./TableRow.jsx";
import TableHeader from "./TableHeader.jsx";
import datadefault from "./datadefault.js";



function Table(props){
    const [currentData,setData] = useState(props.data);

    function addRow(){
        var newArray = [...currentData];
        newArray.push(datadefault);
        setData(newArray);
    }

    function removeRow(id){
        var newArray = [...currentData];
        console.log(id);
        console.log(newArray);
        newArray.splice(id,1);
        console.log(newArray);
        setData(newArray);
    }

    function handleChange(name,value,id){
        setData(old =>
            old.map((row, index) => {
              if (index === id) {
                return {
                  ...old[id],
                  [name]: value,
                }
              }
              return row
            })
          )
    }

    return <div><table className="table table-sm table-hover">
          <thead>
        <tr>
        {columns.map((column,index)=>(<TableHeader key={index} header={column.Header}/>))}
        </tr>
    </thead>
        <tbody>{currentData.map((row,index)=><TableRow id={index} key={index} rowData={row} removeRow={removeRow} handleChange={handleChange}/>)}</tbody></table>
        <button onClick={addRow} className="btn btn-success">Add Row</button>
        <button onClick={props.submitData} className="btn btn-danger">Submit Data</button>
  </div>
}


export default Table;
