import React from "react";
import TableCell from "./TableCell.jsx";

function TableRow(props){
    const keys = Object.keys(props.rowData);
    const values = Object.values(props.rowData);

    function handleChange(name,value){
        props.handleChange(name,value,props.id);
    }

    return <tr>{keys.map((thisKey,index)=> <TableCell key={index} id={index} name={thisKey} value={values[index]} handleChange={handleChange}/>)}
    <td><button className="btn btn-danger" onClick={()=>props.removeRow(props.id)}>X</button></td>
    </tr>
}

export default TableRow;