import React from "react";
import columns from "./columns.js";

function TableCell(props){

    function handleChange(event){
        var changedText = event.target.value;
        props.handleChange(props.name,changedText);
    }

    function highlight(event){
        event.target.select();
    }

    const inputStyle = {
        width: columns[props.id].defaultWidth,
        borderColor: "AliceBlue"
    }


    return <td><input onClick={highlight} style={inputStyle} onChange={handleChange} value={props.value}></input></td>
}


export default TableCell;